import React from "react";
import { Link } from "react-router-dom";
import copy from "copy-to-clipboard";
import "./CardsMenu.css";
import { ReactComponent as CloseIcon } from "assets/close_icon.svg";
import { ReactComponent as CopyIcon } from "assets/copy_icon.svg";
import { ReactComponent as ExternalIcon } from "assets/external_icon.svg";

class CardsMenu extends React.Component {
  componentDidMount() {
    document.documentElement.className = "js";
  }

  state = {
    menuOpen: false,
    copyEmailTooltip: "Click to copy",
  };

  open = () => {
    this.setState({ menuOpen: true });
  };

  close = () => {
    this.setState({ menuOpen: false });
  };

  copyEmailToClipboard = () => {
    copy("mailtoashwinnayar@gmail.com");
    this.setState({ copyEmailTooltip: "Copied!" });
  };

  render() {
    return (
      <div className="demo-4">
        <div className={`menu ${this.state.menuOpen ? "menu--open" : ""}`}>
          <button
            id="btn-menu-close"
            className="btn btn--menu-close"
            aria-label="Close menu"
            onClick={this.close}
          >
            <CloseIcon className="icon" />
          </button>
          <nav className="nav-links">
            <Link to="/" onClick={this.close}>
              <h1>Home</h1>
            </Link>
            <Link to="/about" onClick={this.close}>
              <h1>About Me</h1>
            </Link>
            <a href="https://www.github.com/glocore" target="__blank">
              <h1>GitHub</h1>
              <ExternalIcon />
            </a>
            <a href="https://www.linkedin.com/in/glocore/" target="__blank">
              <h1>LinkedIn</h1>
              <ExternalIcon />
            </a>
            <a
              href="https://drive.google.com/file/d/10z_t9qNmINPAOqKzMzpxWUPlgSMH4XCr/view"
              target="__blank"
            >
              <h1>Resume</h1>
              <ExternalIcon />
            </a>
            <button
              onClick={this.copyEmailToClipboard}
              className="btn__email"
              tooltip={this.state.copyEmailTooltip}
            >
              <h1>Email</h1>
              <CopyIcon style={{ width: "20px" }} />
            </button>
          </nav>
        </div>
        <div className={`page ${this.state.menuOpen ? "page--move" : ""}`}>
          <div className="page__folder page__folder--dummy" />
          <div className="page__folder page__folder--dummy" />
          <div className="page__folder page__folder--dummy" />
          <main className="main-wrap page__folder">{this.props.children}</main>
        </div>
      </div>
    );
  }
}

export default CardsMenu;
